.categories-page {
    margin-top: 50px;
    background-color: #3B414B;
    width: 100%;
    height: 500px;
}

.catigories-header {
    align-items: center;
    border-bottom: 1px solid black;
    margin-top: 1rem;
}

.catigories-header-main {
    margin-top: 1rem;
    align-items: center;
}

.categories-name {
    font-family: Oxygen, sans-serif;
    font-size: 50px;
    margin-top: 1rem;
    margin-left: 1rem;
    margin-right: 1rem;
    color: #fff;
    text-transform: uppercase;
}

.catogories-main-text {
    margin-top: -.5rem;
    margin-left: 1rem;
    margin-bottom: 0.5rem;
    color: #fff;
}

.category {
    border-bottom: 1px solid #000;
    margin: 0.8rem 20px;
    /* margin-top: 0.8rem 0.8rem; */
}

.status-closed {
    background-color: rgb(231, 43, 43);
}

.forum-logo {
    display: block;
    font-size: 10px;
}