.topic {
    margin-top: 3rem;
    
}

.topic-header-row {
    height: 400px;
    background: #3B414B;
    color: #fff;
}

.topic-header {
    margin-top: 2rem;
    background: #3B414B;
    color: #fff;
    border-right: 1px solid #26272b;
}

.topic-header > img {
    max-height: 100px;
    border-radius: 10px;
}

.topic-header-owner-nickname {
    margin-top: .5rem;
    margin-bottom: .3rem;
    font-size: 30px;
}

.topic-moderator {
    background: linear-gradient(120deg, #a8e401, #eb4bd7);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-transform: uppercase;
    /* color: #0B2349; */
    display: table;
    font-size: 20px;
}

.topic-header-owner-role {
    font-size: 15px;
    text-transform: uppercase;
}

.topic-main-message {
    margin-top: 2rem;
    font-size: 20px;
}

.topic-chat {
    margin-top: 2rem;
    /* margin-left: 30%;    */
}


.topic-chat-write-area {
    width: 1300px;
    max-height: 144px;
    background: #1D2129;
    border: none;
    color: #fff;
}

.topic-chat-bottom-area {
    width: 1300px;
    height: 58px;
    background: #222833;
}

.topic-chat-button-post {
    float:right;
    margin-right: 1rem;
    margin-top: .5rem;
}

.topic-messages {
margin-top: 2rem;
}

.topic-message {
    /* width: 900px; */
    height: 'auto';
    background: #1D2129;
    color: white;
    margin-top: 2rem;
}

.topic-message > img {
    max-height: 680px;
    max-width: 550px;
    margin: auto;
    display: flex;
}


.topic-close {
    margin-top: .7rem;
    
}
.topic-close > span {
    padding: 5px;
    background-color: red;
    border-radius: 7px;
}

.topic-system {
    margin-top: 1rem;
    font-size: 25px;
}


@media screen and (min-width: 1200px) and (max-width: 1400px) {
    .topic-chat {
        max-width: 1100px;
    }
    .topic-chat-write-area {
        max-width: 1100px;
    }

    .topic-chat-bottom-area {
        max-width: 1100px;
    }

    .topic-message {
        max-width: 1100px;
    }
}

@media screen and (min-width: 993px) and (max-width: 1200px) {
    .topic-chat-write-area {
        max-width: 950px;
    }

    .topic-chat-bottom-area {
        max-width: 950px;
    }

    .topic-message {
        max-width: 950px;
    }
}

@media screen and (min-width: 768px) and (max-width: 993px) {
    .topic-chat {
        max-width: 700px;
    }

    .topic-chat-write-area {
        max-width: 700px;
    }

    .topic-chat-bottom-area {
        max-width: 700px;
    }

    .topic-message {
        max-width: 700px;
    }

    .topic-message > img {
        max-width: 330px;
        margin: auto;
        display: flex;
    }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .topic-chat {
        width: 100%;
        margin: 0;
    }

    .topic-chat-write-area {
        margin-top: 50px;
        max-width: 516px;
    }

    .topic-chat-bottom-area {
        max-width: 516px;
    }

    .topic-message {
        max-width: 516px;
    }

    .topic-message > img {
        max-width: 460px;
        margin: auto;
        display: flex;
    }
}

@media screen and (max-width: 576px) {
    .topic-chat {
        width: 100%;
        margin: 0;
    }

    .topic-chat-write-area {
        margin-top: 50px;
        max-width: 300px;
    }

    .topic-chat-bottom-area {
        max-width: 300px;
    }

    .topic-message-header {
        margin-top: 0;
    }

    .topic-message {
        max-width: 300px;
        height:auto;
        padding-top: 5px;
        padding-bottom: 5px;
    }

    .topic-message:not(:first-child) {
        margin-top: 15px;
    }

    .topic-message > img {
        max-width: 260px;
        margin: auto;
        display: flex;
    }
}