.chat {
    margin-top: 2rem;
    margin-left: 6rem;
}

.chat-write-area {
    width: 900px;
    max-height: 144px;
    background: #1D2129;
    border: none;
    color: #fff;
}


/* .chat-write-area {
    width: 900px;
    height: 144px;
    background: none;
    
} */

.chat-bottom-area {
    width: 900px;
    height: 58px;
    background: #222833;
}

.chat-button-post {
    float:right;
    margin-right: 1rem;
    margin-top: .5rem;
}

.messages {
    margin-top: 2rem;
}

.message {
    width: 900px;
    height: 'auto';
    background: #1D2129;
    color: white;
    margin-top: 1.8rem;
}

.message-header {
    padding-top: .5rem;
    margin-left: 1rem;
}

.message-trash {
    padding-top: 1rem;
    margin-right: .7rem;
}

.post {
    margin-top: 1rem;
    margin-left: 2rem;
}

.post > img {
    max-height: 680px;
    max-width: 550px;
    margin: auto;
    display: flex;
    padding-bottom: 2rem;
}

.message-avatar > img {
    max-width: 40px;
    border-radius: 15%;
}

.message-nickname {
    font-family: Oxygen, sans-serif;
    margin-left: 1rem;
    font-size: 18px;
    margin-top: 0.5rem;
}

.date {
    margin-left: 1rem;
}

@media screen and (min-width: 1200px) and (max-width: 1400px) {
    .chat {
        max-width: 700px;
    }
    .chat-write-area {
        max-width: 700px;
    }

    .chat-bottom-area {
        max-width: 700px;
    }

    .message {
        max-width: 700px;
    }
}

@media screen and (min-width: 993px) and (max-width: 1200px) {
    .chat-write-area {
        max-width: 600px;
    }

    .chat-bottom-area {
        max-width: 600px;
    }

    .message {
        max-width: 600px;
    }
}

@media screen and (min-width: 768px) and (max-width: 993px) {
    .chat {
        margin-left: 3rem;
    }

    .chat-write-area {
        max-width: 400px;
    }

    .chat-bottom-area {
        max-width: 400px;
    }

    .message {
        max-width: 400px;
    }

    .post > img {
        max-width: 330px;
        margin: auto;
        display: flex;
    }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .chat {
        width: 100%;
        margin: 0;
    }

    .chat-write-area {
        margin-top: 50px;
        max-width: 516px;
    }

    .chat-bottom-area {
        max-width: 516px;
    }

    .message {
        max-width: 516px;
    }

    .post > img {
        max-width: 460px;
        margin: auto;
        display: flex;
    }
}

@media screen and (max-width: 576px) {
    .chat {
        width: 100%;
        margin: 0;
    }

    .chat-write-area {
        margin-top: 50px;
        max-width: 300px;
    }

    .chat-bottom-area {
        max-width: 300px;
    }

    .message-header {
        margin-top: 0;
    }

    .message {
        max-width: 300px;
        height:auto;
        padding-top: 5px;
        padding-bottom: 5px;
    }

    .message:not(:first-child) {
        margin-top: 15px;
    }

    .post > img {
        max-width: 260px;
        margin: auto;
        display: flex;
    }
}