.navbar {
    width: auto;
    height: 107px;
    background: #3B414B;
}

.navbar-logo > img{
    width: 83px;
}

.navbar-text {
    display: flex;
    margin-top: 1rem;
}

.navbar-text-forum, .navbar-text-users {
    font-family: Oxygen, sans-serif;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    text-decoration: none;
    color: #B3DBF0;
}

.navbar-text-users {
    margin-left: 100px;
}

.navbar-text-forum:hover, .navbar-text-users:hover {
    color: #A2A6B2
}

.navbar-avatar {
    margin-right: 15px;
    cursor: pointer;
}

.navbar-avatar > img {
    width: 55px;
    border-radius: 50%;
}

@media screen and (min-width: 768px) and (max-width: 993px) {
    .navbar-text-forum, .navbar-text-users {
        font-size: 18px;
    }

    .navbar-text-users {
        margin-left: 50px;
    }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .navbar-text-forum, .navbar-text-users {
        font-size: 17px;
    }

    .navbar-text {
        margin-left: -20px;
    }
    .navbar-text-users {
        margin-left: 30px;
    }
}

@media screen and (max-width: 576px) {
    .navbar-logo > img{
        width: 83px;
    }
    
    .navbar-text-forum, .navbar-text-users {
        font-size: 12px;
    }

    .navbar-text {
        margin-left: -25px;
    }
    .navbar-text-users {
        margin-left: 30px;
    }

    .navbar-avatar {
        float: right;
    }
}