.forum {
    margin-top: 2rem;
}

.forum-window {
    width: 900px;
    height: auto;
    background-color: #313742;
    border-radius: 15px ;
    margin-bottom: 35px;
    padding-bottom: 10px;
}

.forum-window-header {
    /* background: linear-gradient(150deg, #53f 15%, #05d5ff 70%, #a6ffcb 94%); */
    width: auto;
    height: 2.5rem;
    border-radius: 15px 15px 0 0;
    color: #fff;
    font-size: 25px;
    font-family: Oxygen, sans-serif;
    padding-left: 1rem;
    margin-bottom: .5rem;
}

.forum-line {
    width: auto;
    height: 2.8em;
    border: 2px solid #1f2227;
    border-radius: 10px;
    padding-left: .75rem;
    margin-bottom: .1rem;
}

.forum-logo-module-forum, .forum-name, .forum-last-user {
    /* border-right: 1.5px solid #333841; */
    overflow: 1;
}

.forum-logo-module-forum {
    color: #fff;
    font-size: 25px;
}

.forum-name {
    color: #fff;
    font-size: 19px;
    margin-top: .2rem;
}

.status {
    margin-top: .2rem;
    margin-bottom: .4rem;
    margin-right: 1rem;
    font-size: 12px;
    padding: 5px 10px;
    background: orange;
    border-radius: 5px;
    color: #000;
}

.forum-last-user > img {
    max-width: 40px;
    border-radius: 50%;
}

.forum-last-nickname {
    margin-left: 0.5rem;
    color: #7f8286;
    font-size: 15px;
}

.forum-last-nickname > a {
    text-decoration: none;
    color: #fff;
}

@media screen and (min-width: 1200px) and (max-width: 1400px) {
    .forum-window {
        max-width: 750px;
    }
}

@media screen and (min-width: 993px) and (max-width: 1200px) {
    .forum-window {
        max-width: 600px;
    }

    .forum-name {
        font-size: 15px;
    }

    .status {
        padding: 0;
        margin-bottom: .8rem;
    }
}

@media screen and (min-width: 768px) and (max-width: 993px) {
    .forum-window {
        width: 700px;
    }

    .forum-name {
        font-size: 15px;
    }

    .status {
        padding: 0;
        margin-bottom: .8rem;
    }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .forum-window {
        width: 500px;
    }

    .forum-logo-module-forum {
        display: none;
    }

    .forum-name {
        font-size: 15px;
    }

    .status {
        padding: 0;
        margin-bottom: .8rem;
    }
}

@media screen and (max-width: 576px) {
    .forum-window {
        max-width: 93vw;
    }

    .forum-logo-module-forum {
        display: none;
    }

    .forum-name {
        margin-top: 0.5rem;
        font-size: 13px;
    }

    .status {
        padding: 0;
        margin-bottom: .8rem;
    }

    .forum-last-user > img {
        width: 40px;
        height: 40px;
    }

    .forum-last-nickname {
        margin-left: 0.5rem;
        color: #7f8286;
        font-size: 12px;
        margin-top: 0.2rem;
    }
}