main {
    width: auto;
    min-height: 100vh
}

.profile-background {
    max-width: 1300px;
    height: 120px;
    margin-top: 20px;
    background: #3B414B;
}

.profile-bottom-div { 
    max-width: 1300px;
    height: 157px;   
    background: #313742;
}

.profile-avatar {
    margin-top: 15px;
    margin-left: .75rem;
}

.profile-avatar > img {
    max-height: 200px;
    border-radius: 15px;
}

.btn-delete > button {
    border-radius: 50%;
    margin-top: -5px;
    margin-right: 3px;
}

.profile-nickname {
    font-family: Oxygen, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 44px;
    line-height: 61px;
    color: #FFFFFF;
    margin-left: 1rem;
}

.icons{
    margin-left: calc(338px - 6rem);
}

.icon-letter {
    margin-left: 1rem;
}

.icon-clock {
    margin-left: 3rem;
}

.icon-message > img {
    width: 40px;
} 

.icon-clock > img {
    width: 40px;
}

.btn-unlogin {
    margin-left: 1rem;
}


.counter-message {
    color: #fff;
    font-family: Oxygen, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 61px;
    text-align: center;
}

.counter-clock {
    color: #fff;
    font-family: Oxygen, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 61px;
    text-align: center;
    margin-left: -8px;
}


@media screen and (min-width: 1200px) {

}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .profile-header-chat {
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 576px) {
    .profile-nickname {
        font-size: 35px;
    }

    .profile-avatar > img {
        max-width: 100px;
    }

    .icons {
        margin-left: 5rem;
        padding-top: 1rem;
    }

    .profile-header-chat {
        flex-wrap: wrap;
    }
}