.left-column {
    margin-top: 2rem;
    width: 350px;
    height: 519px;
    background: #313742;
}

.role {
    max-width: 340px;
    height: 40px;
    border-radius: 25px;
    margin: 10px 1rem 0;
    text-align: center;
    font-size: 20px;
    padding-top: .3rem;
}

.role:not(:first-child) {
    margin-top: 1rem;
}
.moderator {
    background: linear-gradient(150deg, #53f 15%, #05d5ff 70%, #a6ffcb 94%);
}

.coder {
    background: linear-gradient(150deg, #c821c9, #c29d52);
} 

.none {
    background: #fff;
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .left-column {
        width: 100%;
        height: auto;
        margin: 0;
        padding: 30px 0 20px 0;
    }

    .role {
        max-width: 95%;
    }
}

@media screen and (max-width: 576px) {
    .left-column {
        width: 100%;
        height: auto;
        margin: 0;
        padding: 30px 0 20px 0;
    }

    .role {
        max-width: 95%;
    }
}